/* styles.css */

body {
    font-family: 'Lexend';
    background-color: #f4f4f4;
    margin: 0;
    padding: 0;
  }
  
  .container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
  }
  
  h1 {
    color: #333;
    font-size: 28px;
    font-weight: 700;
    margin-bottom: 20px;
  }
  
  p {
    color: #666;
    font-size: 16px;
    line-height: 1.6;
    margin-bottom: 15px;
  }
  
  ul {
    color: #666;
    font-size: 16px;
    line-height: 1.6;
    margin-bottom: 15px;
    padding-left: 20px;
  }
  
  li {
    margin-bottom: 5px;
  }
  
  .btn {
    display: inline-block;
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    text-decoration: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
  }
  
  .btn:hover {
    background-color: #0056b3;
  }
  