body {
    font-family: 'Lexend', sans-serif;
  }
  
  .reset-password-container {
    max-width: 400px;
    margin: 50px auto;
    padding: 20px;
    border-radius: 8px;
    background-color: #f9f9f9;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  h2 {
    text-align: center;
    color: #333;
  }
  
  .form-group {
    margin-bottom: 20px;
  }
  
  label {
    color: #555;
    display: block;
    margin-bottom: 8px;
  }
  
  input {
    padding: 10px;
    width: calc(100% - 20px);
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
  }
  
  button {
    padding: 10px;
    background-color: #3490E4;
    color: #fff;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
    width: 100%;
    transition: background-color 0.3s;
  }
  
  button:hover {
    background-color: #3490E4;
  }

button:active {
    background-color: #225280;
}
  
  .error {
    margin-top: 10px;
    color: red;
    text-align: center;
  }
  
  .success {
    margin-top: 10px;
    color: #3490E4;
    text-align: center;
  }
  